.roomSelectorRoot {
    width: 100vw;
    height: 100vh;
    overflow-y: scroll;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    z-index: 9999;
}

.scroller {
    display: flex;
    flex-wrap: wrap;

    gap: 3vh;
}

.collectionBox {
    width: 300px;
    /* height: 120px; */
    position: relative;
    overflow: hidden;
    border-radius: 25px;

    box-shadow: 0px 0px 10px 2px #39393946;
}

.collectionBox img{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    top: 0px;
    left: 0px;
    object-fit: cover;
    object-position: 50% 50%;
}

.collectionBox .description{
    height: 0.001px;
}

.collectionBox > .svgOverlay{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 0;
    top: 0px;
    left: 0px;
    object-fit: fill;
    opacity: 0.2;

}

.collectionBox.active > .svgOverlay{
    opacity: 1;

}

.svgOverlay *{
    width: 100%;
    height: 100%;
}

.collectionBox.active .description{
    height: 100px;
    padding: 10px;
}

.description {
    display: flex;
    flex-direction: row;
    align-items: center;
    overflow: hidden;
    opacity: 0.6;
    box-sizing: border-box;
    height: 0.001px;
    padding: 0.001px;
}

.description article{
    color: #ffffff;
    opacity: 1;
    font-size: 13px;
    flex: 0.7;
}

.description .navigatorLinkWrapper{
    color: #ffffff;
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 0.3;
    
}

.navigatorLink {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0px;
    margin: 0px;
    padding: 0px;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    fill: #ffffff;
}

.navigatorLink:hover {
    color: #ffffff;
    fill: #ffffff;
    opacity: 0.2;
}

.navigatorLink:active {
    color: #ffffff;
    fill: #ffffff;
    opacity: 0.2;
}

.navigatorLink svg {
    width: 60%;
}

.navigatorLink span {
    font-size: 10px;
}

.ballerAnim {
    transition: 
      width 0.4s cubic-bezier(.05,.18,.17,.96),
      opacity 0.4s cubic-bezier(.05,.18,.17,.96),
      height 0.4s cubic-bezier(.05,.18,.17,.96),
      transform 0.4s cubic-bezier(.05,.18,.17,.96),
      right 0.4s cubic-bezier(.05,.18,.17,.96),
      padding 0.4s cubic-bezier(.05,.18,.17,.96),
      bottom 0.4s cubic-bezier(.05,.18,.17,.96);
    
    transform: translateX(0.0001px);

}

.background{
    z-index: -1;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
}

.background div {
    width: 100vw;
    height: 100vh;
    z-index: 0;
    position: fixed;
}

.background img {
    object-fit: cover;
    object-position: 50% 50%;
    position: fixed;
    z-index: -2;
    width: 100vw;
    height: 100vh;
}

.iosBlur {
    background-color: #39393946;
    backdrop-filter: blur(33px);
    -webkit-backdrop-filter: blur(33px);
    color: #ffffff;
}